const TYPE_MANIFEST = 'type-manifest'
const actions = {
  initTypesManifest({ commit }, data) {
    commit(TYPE_MANIFEST, data)
  }
}

// 列表都为键值对，例子：{id:1,name:'xxx'}
// 新增下拉列表数据时,再次添加即可.
const state = {
  isOpenList: [
    {
      id: 1,
      name: '开通'
    },
    {
      id: 0,
      name: '未开通'
    }
  ]
}

const mutations = {
  [TYPE_MANIFEST](state, payload) {
    window.sessionStorage.setItem(TYPE_MANIFEST, JSON.stringify(payload))
    for (const key in payload) {
      if (payload.hasOwnProperty(key)) {
        const list = payload[key]
        // list.forEach(x => { x.id = Number(x.id); });
        state[key] = list
      }
    }
  }
}

const getters = {}
// 动态添加列表的getters
for (const key in state) {
  if (state.hasOwnProperty(key)) {
    getters[key] = function(state) {
      if (!state[key] || state[key].length <= 0) {
        const manifestStr = window.sessionStorage.getItem(TYPE_MANIFEST)
        const manifest = manifestStr ? JSON.parse(manifestStr) : {}
        Object.assign(state, manifest)
      }
      return state[key]
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
