import excelProcessor from '@/framework/lib/excel-processor'
const DATA_SOURCE_INFO = 'data-source-info'

const state = {
  dataSources: [],
  activeSourceId: null
}
const getters = {
  activeSourceId: state => state.activeSourceId,
  dataSources: state => state.dataSources
}
const mutations = {
  [DATA_SOURCE_INFO](state, payload) {
    Object.assign(state, payload || {})
  }
}
const actions = {
  setDataSources({ commit }, data) {
    excelProcessor.init(data)
    commit(DATA_SOURCE_INFO, { dataSources: data })
  },
  setActiveSource({ commit }, dsId) {
    commit(DATA_SOURCE_INFO, { activeSourceId: dsId })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}
