const BOARD_DATA = 'board-data'
const ACTIVE_PAGE_VIEW = 'active-page-view'
const ADD_NEW_PAGE = 'add-new-page'
const DEL_PAGE = 'del-page'
const ADD_ELEMENT = 'add-element'
const BINDING_DATA = 'binding-data'

const initPageId = Date.now().toString()
const initPage = {
  id: initPageId,
  title: '新建页面1',
  type: '',
  affix: false,
  live: true,
  active: true,
  pageView: {
    selectedEleId: null,
    components: []
  }
}

const state = {
  pageList: [initPage], //页面列表
  activePageId: initPageId //当前被激活的页面eleId
}
const getters = {
  pageList: state => state.pageList || [],
  activePageId: state => state.activePageId,
  activePage: state => {
    return state.pageList?.find(x => x.id === state.activePageId)
  },
  activeElement: state => {
    const page = state.pageList?.find(x => x.id === state.activePageId)
    if (page?.pageView) {
      const eleId = page.pageView?.selectedEleId
      const eleData =
        page.pageView?.components?.find(x => x.eleId === eleId) || {}
      return eleData
    }
    return {}
  },
  pageComponents: state => {
    if (!state.activePageId) {
      return state.pageList[0]?.pageView?.components || []
    }
    const page = state.pageList.find(x => x.id === state.activePageId)
    return page?.pageView?.components || []
  }
}

const mutations = {
  [BOARD_DATA](state, payload) {
    Object.assign(state, payload)
  },
  [ACTIVE_PAGE_VIEW](state, { pageId, eleId, components }) {
    const curPage = state.pageList?.find(
      x => x.id === pageId || state.activePageId
    )?.pageView
    if (curPage) {
      curPage.selectedEleId = eleId
    }
    if (curPage && components) {
      curPage.components = components
    }
  },
  [ADD_ELEMENT](state, element) {
    const curPage = state.pageList?.find(x => x.id === state.activePageId)
      ?.pageView
    curPage?.components?.push(element)
  },
  [ADD_NEW_PAGE](state, payload) {
    const id = Date.now().toString()
    const newPage = { ...initPage }
    newPage.title = '新建页面'
    newPage.id = id
    state.pageList?.push(newPage)
    state.activePageId = id
  },
  [DEL_PAGE](state, pageId) {
    const index = state.pageList?.findIndex(x => x.id === pageId)
    if (index >= 0) {
      state.pageList?.splice(index, 1)
    }
  },
  [BINDING_DATA](state, bindingData) {
    const page = state.pageList?.find(x => x.id === state.activePageId)
    if (!page?.pageView) return
    const eleId = page.pageView?.selectedEleId
    const eleData =
      page.pageView?.components?.find(x => x.eleId === eleId) || {}
    if (eleData) {
      eleData.binding = bindingData
    }
  }
}

const actions = {
  setBoardInfo({ commit }, data) {
    commit(BOARD_DATA, data)
  },
  setBinding({ commit, getters }, data) {
    const activeEle = getters.activeElement
    if (!activeEle) return
    commit(BINDING_DATA, data)
  },
  setPageList({ commit }, pageList) {
    if (!pageList || pageList.length <= 0) {
      pageList = [initPage]
    }
    commit(BOARD_DATA, { pageList: pageList })
  },
  activePage({ commit }, pageId) {
    commit(BOARD_DATA, { activePageId: pageId })
  },
  setActivePage({ commit }, { pageId, components, eleId }) {
    commit(ACTIVE_PAGE_VIEW, { pageId, components: components, eleId })
  },
  activeEle({ dispatch, commit, getters }, eleId) {
    commit(ACTIVE_PAGE_VIEW, { pageId: getters.activePageId, eleId: eleId })
  },
  addElement({ commit }, ele) {
    commit(ADD_ELEMENT, ele)
    commit(ACTIVE_PAGE_VIEW, { eleId: ele.eleId })
  },
  addNewPage({ commit }, data) {
    commit(ADD_NEW_PAGE, data)
  },
  deletePage({ commit }, pageId) {
    commit(DEL_PAGE, pageId)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}
