export function getInfo(token) {
  const userInfoStr = getUserInfo()
  const userInfo = JSON.parse(userInfoStr)
  const name = userInfo && userInfo.name
  return Promise.resolve({
    name: name,
    // avatar: null,
    introduction: null,
    roles: ['admin']
  })
}

