//TODO:以后需适配app中存储,微信小程序的存储
//本地存储数据,暂时不需要加密,若需求需要加密,
//可使用npm包secure-ls加密.但由于包较大,可使用crypto,若引入crypto-js/aes方式,较少包的体积
export const $localStorage = {
  /**
   *@params expires 过期时间,单位毫秒
   */
  setItem(key, value, expires) {
    const data = {
      value: value,
      time: Date.now(),
      expires: expires
    }
    window.localStorage.setItem(key, JSON.stringify(data))
  },
  getItem(key) {
    const dataStr = window.localStorage.getItem(key)
    if (!dataStr) return dataStr
    const data = JSON.parse(dataStr)
    if (data.expires && Date.now() - data.time > data.expires) {
      window.localStorage.removeItem(key)
      return null
    }
    return data.value
  },
  removeItem(key) {
    window.localStorage.removeItem(key)
  }
}
export const $sessionStorage = {
  setItem(key, value) {
    window.sessionStorage.setItem(key, value)
  },
  getItem(key) {
    return window.sessionStorage.getItem(key)
  },
  removeItem(key) {
    window.sessionStorage.removeItem(key)
  }
}
