import { login, logout, getInfo } from '@/api/user'
import { getToken, setToken, removeToken, setUserInfo } from '@/lib/app/auth'
import router, { resetRouter } from '@/router'

const state = {
  token: getToken(),
  name: '',
  userInfo: null,
  avatar: '',
  introduction: '',
  roles: []
}
const getters = {
  //登录名
  userName: state => {
    if (!state.userInfo) return
    return state.userInfo.userName
  },
  // 用户姓名
  realName: state => {
    if (!state.userInfo) return
    return state.userInfo.realName || state.userInfo.userName
  }
}
const mutations = {
  SET_TOKEN: (state, token) => {
    state.token = token
  },
  SET_INTRODUCTION: (state, introduction) => {
    state.introduction = introduction
  },
  SET_NAME: (state, name) => {
    state.name = name
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar
  },
  SET_ROLES: (state, roles) => {
    state.roles = roles
  },
  SET_USERINFO: (state, userInfo) => {
    state.userInfo = userInfo
  }
}

const actions = {
  // user login
  login({ commit }, userInfo) {
    const { username, password } = userInfo
    return new Promise((resolve, reject) => {
      login({ username: username.trim(), password: password })
        .then(() => {
          // const { data } = response
          // commit('SET_TOKEN', data.token)
          // setToken(data.token)
          commit('SET_NAME', username)
          resolve()
        })
        .catch(error => {
          reject(error)
        })
    })
  },

  // get user info
  getInfo({ commit, state }) {
    return new Promise((resolve, reject) => {
      getInfo(state.token)
        .then(userInfo => {
          if (!userInfo) {
            reject('Verification failed, please Login again.')
          }
          userInfo.name = userInfo.name || state.name
          setUserInfo(userInfo)
          const { roles, name, avatar, introduction } = userInfo
          // const roles = userInfo.roles;
          // const name = userInfo.name;
          // const avatar = userInfo.avatar;
          // const introduction = userInfo.introduction;
          // roles must be a non-empty array
          if (!roles || roles.length <= 0) {
            reject('getInfo: roles must be a non-null array!')
          }
          commit('SET_NAME', name)
          commit('SET_ROLES', roles)
          commit('SET_AVATAR', avatar)
          commit('SET_INTRODUCTION', introduction)
          resolve({
            roles: roles,
            name: name,
            avatar: avatar,
            introduction: introduction
          })
        })
        .catch(error => {
          reject(error)
        })
    })
  },

  // user logout
  logout({ commit, state, dispatch }) {
    return new Promise((resolve, reject) => {
      logout(state.token)
        .then(() => {
          commit('SET_TOKEN', '')
          commit('SET_ROLES', [])
          setUserInfo(null)
          removeToken()
          resetRouter()
          // reset visited views and cached views
          dispatch('tagsView/delAllViews', null, { root: true })
          resolve()
        })
        .catch(error => {
          reject(error)
        })
    })
  },

  // remove token
  resetToken({ commit }) {
    return new Promise(resolve => {
      commit('SET_TOKEN', '')
      commit('SET_ROLES', [])
      removeToken()
      resolve()
    })
  },

  // dynamically modify permissions
  changeRoles({ commit, dispatch }, role) {
    return new Promise(resolve => {
      const token = role + '-token'
      commit('SET_TOKEN', token)
      setToken(token)
      const { roles } = dispatch('getInfo')
      resetRouter()
      // generate accessible routes map based on roles
      const accessRoutes = dispatch('', roles, { root: true })
      // dynamically add accessible routes
      router.addRoutes(accessRoutes)
      // reset visited views and cached views
      dispatch('tagsView/delAllViews', null, { root: true })
      resolve()
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
