import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import pageRoutes from './modules/index'
import defaultRoutes from './constant-routes'
/**
 * constantRoutes
 * a base page that does not have permission requirements
 * all roles can be accessed
 */
export const constantRoutes = defaultRoutes
/**
 * asyncRoutes
 * the routes that need to be dynamically loaded based on user roles
 */
const otherPageRoutes = [{ path: '*', redirect: '/404', hidden: true }]
export const asyncRoutes = pageRoutes.concat(otherPageRoutes)

const createRouter = () =>
  new VueRouter({
    // mode: 'history', // require service support
    // base: process.env.BASE_URL,
    // scrollBehavior: () => ({ y: 0 }),
    routes: constantRoutes
  })

const router = createRouter()

// Detail see: https://github.com/vuejs/vue-router/issues/1234#issuecomment-357941465
export function resetRouter() {
  // const newRouter = createRouter()
  // router.matcher = newRouter.matcher // reset router
}
export default router
