/**
 * @name axios通信
 * @author mayuxian
 * @description 详细配置可以查看npm官网库中axios说明
 */
// const axios = require('axios')
import axios from 'axios'
const axiosInstance = axios.default

import { getSign } from './signer'
import eventBus from '@/lib/event-bus'
import { errorMap } from './error-map'
import qs from 'qs'
// 确定这种方式,是否只有下载时才请求
// @vue/cli-shared-utils中间接以来qs
const _formContentType = 'application/x-www-form-urlencoded'
const _jsonContentType = 'application/json'
const _requestTypeEnum = { json: 'json', form: 'form' }
// const _excludeReqMethods = ['get', 'delete'];  //这两个方法不处理req
axiosInstance.defaults.baseURL = process.env.VUE_APP_BASE_API
const timeout = process.env.VUE_APP_ENV === 'production' ? 10000 : 30000
axiosInstance.defaults.timeout = timeout // 默认5秒超时
// axiosInstance.defaults.maxContentLength =-1;
axiosInstance.defaults.withCredentials = true

// if (process.env.NODE_ENV == 'development') {
//   axiosInstance.defaults.headers.common['pcpopclub'] = process.env.VUE_APP_TOKEN
// }
axiosInstance.defaults.headers.common['x-requested-with'] = 'XMLHttpRequest'

axiosInstance.interceptors.request.use(
  config => {
    const header = config.headers[config.method]
    const _reqType = config.requestType && config.requestType.toLowerCase()
    if (_reqType) {
      if (!_requestTypeEnum[_reqType]) {
        // 未识别出类型,则抛出异常
        const err = new Error(
          `${config.url}请求类型requestType:${config.requestType}配置错误(有效值:json/form)`
        )
        eventBus.$emit('requestError', err)
        return Promise.reject(err)
      }
      header['Content-Type'] =
        _reqType === _requestTypeEnum.json ? _jsonContentType : _formContentType
    }
    if (header['Content-Type'] === _formContentType && config.data) {
      config.data = qs.stringify(config.data)
    }
    return config
  },
  error => {
    console.error('requestError', error)
    eventBus.$emit('requestError', error)
    return Promise.reject(error)
  }
)

axiosInstance.interceptors.response.use(
  response => {
    const data = response.data
    if (data.returncode !== 0) {
      console.warn('responseFail', data)
      const reqErr = new Error(data.error || '通信异常')
      reqErr.name = data.status
      reqErr.stack = data.message
      eventBus.$emit('responseFail', reqErr)
      return Promise.reject(data)
    } else {
      return data.result
    }
  },
  error => {
    eventBus.$emit('responseError', error)
    const err = errorMap[error.message] || error.message
    error.message = err
    console.error('responseError', error)
    return Promise.reject(error)
  }
)

//过滤掉值为空的字段
const obj2Params = params => {
  const reqParams = {}
  for (const key in params) {
    if (params.hasOwnProperty(key)) {
      const val = params[key]
      if (
        (typeof val === 'string' && !val) ||
        typeof val === 'undefined' ||
        val === null
      ) {
        continue
      }
      reqParams[key] = val
    }
  }
  return reqParams
}

let $getSign = null
export const configSigner = signer => {
  $getSign = signer.getSign
}

// export default axiosInstance
export default ['get', 'post', 'put', 'patch', 'delete', 'options'].reduce(
  (api, method) => {
    api[method] = ({ url, params, type, ...config }) => {
      const opts = { url, method, type, ...config }
      opts.withCredentials = true
      const reqParams = obj2Params(params || {}) //过滤掉值为空的字段
      const _getSign = $getSign || getSign
      //TODO:web必须需要传版本?
      reqParams._timestamp = new Date().getTime()
      reqParams._sign = _getSign(reqParams)
      if (['put', 'post', 'patch'].includes(method)) {
        opts.data = reqParams
        opts.requestType = opts.requestType || opts.type || 'json'
      } else {
        opts.params = reqParams
      }
      return axiosInstance(opts)
    }
    return api
  },
  {}
)
