const state = {
  tagsView: true,
  fixedHeader: true,
  sidebarLogo: true,
  dataSourceUnion: false
}

const mutations = {
  CHANGE_SETTING: (state, data) => {
    Object.assign(state, data)
  }
}
const getters = {
  dsUnionVisible: state => state.dataSourceUnion
}

const actions = {
  changeSettings({ commit }, data) {
    commit('CHANGE_SETTING', data)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
