<template>
  <section class="navbar">
    <!-- <hamburger
      id="hamburger-container"
      :is-active="sidebar.opened"
      class="hamburger-container"
      @toggleClick="toggleSideBar"
    />-->
    <logo />
    <div class="navbar-command" v-if="false">
      <el-button icon="el-icon-document-add" size="small" type="primary">页面</el-button>
      <el-button icon="el-icon-document-add" size="small" type="primary">工作表</el-button>
    </div>
    <div class="right-menu">
      <template v-if="device !== 'mobile'">
        <screenfull id="screenfull" class="right-menu-item hover-effect" />
        <el-tooltip content="Global Size" effect="dark" placement="bottom">
          <size-select id="size-select" class="right-menu-item hover-effect" />
        </el-tooltip>
      </template>
      <div class="avatar-container right-menu-item hover-effect">
        <i class="el-icon-s-custom"></i>
        <span class="username">{{ name || '用户名' }}</span>
        <div class="hr"></div>
        <el-button type="text" class="exit" @click="logout">退出</el-button>
      </div>
    </div>
  </section>
</template>

<script>
import { mapGetters } from 'vuex'
import Screenfull from '@/components/Screenfull'
import SizeSelect from '@/components/SizeSelect'
import Logo from './Logo'
export default {
  components: {
    Screenfull,
    SizeSelect,
    Logo,
  },
  computed: {
    ...mapGetters(['sidebar', 'avatar', 'device', 'name']),
  },
  methods: {
    toggleSideBar() {
      this.$store.dispatch('app/toggleSideBar')
    },
    async logout() {
      await this.$store.dispatch('user/logout')
      this.$router.push(`/login?redirect=${this.$route.fullPath}`)
    },
  },
}
</script>

<style lang="scss" scoped>
.navbar-command {
  line-height: 50px;
  margin-left: 30px;
  display: inline-block;
}
.hr {
  display: inline-block;
  width: 2px;
  background-color: #606266;
  opacity: 0.6;
  height: 24px;
  margin: 0px 5px;
  vertical-align: middle;
}
.exit {
  color: #606266;
}
.username {
  font-size: 14px;
  color: #606266;
  margin-left: 10px;
}
.navbar {
  height: 50px;
  overflow: hidden;
  position: relative;
  background: #fff;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);

  .hamburger-container {
    line-height: 46px;
    height: 100%;
    float: left;
    cursor: pointer;
    transition: background 0.3s;
    -webkit-tap-highlight-color: transparent;

    &:hover {
      background: rgba(0, 0, 0, 0.025);
    }
  }

  .errLog-container {
    display: inline-block;
    vertical-align: top;
  }

  .right-menu {
    float: right;
    height: 100%;
    line-height: 50px;

    &:focus {
      outline: none;
    }

    .right-menu-item {
      display: inline-block;
      padding: 0 8px;
      height: 100%;
      font-size: 18px;
      color: #5a5e66;
      vertical-align: text-bottom;

      &.hover-effect {
        cursor: pointer;
        transition: background 0.3s;

        &:hover {
          background: rgba(0, 0, 0, 0.025);
        }
      }
    }

    .avatar-container {
      margin-right: 30px;

      .avatar-wrapper {
        margin-top: 5px;
        position: relative;

        .user-avatar {
          cursor: pointer;
          width: 40px;
          height: 40px;
          border-radius: 10px;
        }

        .el-icon-caret-bottom {
          cursor: pointer;
          position: absolute;
          right: -20px;
          top: 25px;
          font-size: 12px;
        }
      }
    }
  }
}
</style>
