class ExcelProcessor {
  private _dataSources: any = []
  init(dataSources: any): void {
    this._dataSources = dataSources
  }
  getFieldData(dataSourceId: string, sheetId: string, fieldId: string) {
    const ds = this._dataSources.find((x: any) => x.id === dataSourceId)
    if (!ds) return null
    const sheet = ds.sheets.find((x: any) => x.name === sheetId)
    if (!sheet) return null
    return sheet.data?.map((item: any) => item[fieldId])
  }
  getAllFields(dataSourceId: string, sheetId: string) {
    const ds = this._dataSources.find((x: any) => x.id === dataSourceId)
    return ds?.sheets.find((x: any) => x.name === sheetId)?.header || []
  }
}

export default new ExcelProcessor()
