import md5 from 'js-md5'
const SIGN_KEY = process.env.VUE_APP_HTTP_SIGNKEY

export const getSign = function(params, signKey) {
  const _signKey = signKey || SIGN_KEY
  if (!params || Object.keys(params).length <= 0) return md5(_signKey)
  let sortParamsStr = ''
  const excludeKeys = ['_sign', 'sign']
  Object.keys(params)
    .sort()
    .forEach(key => {
      const val = params[key]
      if (!excludeKeys.includes(key)) {
        sortParamsStr += Array.isArray(val) ? `${key}[${val}]` : `${key}${val}`
      }
    })
  return md5(_signKey + sortParamsStr + _signKey).toUpperCase()
}
